import _ from 'lodash';

import React, { Component } from 'react';
import { connect } from 'react-redux';

import SearchBar from './search_bar';
import SearchResults from './search_results';
import { addPlaylist } from '../../actions/playlistActions'
import { addRecommended } from '../../actions/settingsActions'
import { videoSearchService, getPlaylist, getRelatedVideos } from '../../services/ytApiService'

import imgLoading from '../../images/searchField/loading.gif'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'


class SearchField extends Component {
  constructor(props) {
    super(props);

    this.state = {
      videos: [],
      selectedVideo: null,
      maxResults : 7,
      playlistImportInputValue : '',
      loading : false,
      search: true,
      relatedVideos: []
    };

    this.videoSearch('snopp dogg dr dre g unit ice cube');
    this.getRelatedVideos();
  }

  videoSearch(term) {
    videoSearchService(this.props.apiKey,term, this.props.maxResults, this.props.onlyHd ? 'high' : 'any', (videos) => {
      this.setState({
        videos: videos,
        selectedVideo: videos[0]
      });
    })
  }

  addVideo = (video) => {
      this.props.addVideo(video);
      const selectedVideo = video;
      this.setState({ selectedVideo })
  }

  getPlaylistService = async () => {
    this.setState({loading : true});
    var videos = [] 
    
    await getPlaylist(this.props.apiKey,this.state.playlistImportInputValue)
      .then((value) => videos = value)

    if(videos) 
      this.props.addPlaylistD(videos);
    
    this.setState({loading : false})
  }

  getRelatedVideos =  async () => { 
      await getRelatedVideos(this.props.apiKey,this.props.currentVideoId)
        .then((value) => { 
          this.setState({ relatedVideos: value }, 
          () => this.props.addRecommended(this.selectRelatedVideo())) 
      })  
  }

  selectRelatedVideo = () => {
    for(var i = 0; i < this.state.relatedVideos.length; i++) {
      if(!this.props.playlist.map(v => v.id.videoId).includes(this.state.relatedVideos[i].id.videoId))
        return this.state.relatedVideos[i];
    }
    return this.state.relatedVideos[this.state.relatedVideos.length-1]
  }

  onPlaylistImportInputChange = (event) => {
    const playlistImportInputValue = event.target.value;
    this.setState({playlistImportInputValue})
  }


  getSnapshotBeforeUpdate(prevProps, prevState) {
    if(prevProps.currentVideoId !== this.props.currentVideoId) {
      this.getRelatedVideos();
    }
    return null;
  }

  render() {
    const videoSearch = _.debounce(term => {
      this.videoSearch(term);
    }, 300);
    return (
      <Col>
      <div className='search-container'>
        <div className="centerisedDiv">
          <Container className="container option-table-container">
            <Row>
              <Col className="option" onClick={() => this.setState({search: true})}>
                <div className={this.state.search ? "option-selected" : "option-not-selected"}>
                  <h4 className={this.state.search ? "selected" : "not-selected"}>Youtube Search</h4>
                </div>
              </Col>
              <Col className="option" onClick={() => this.setState({search: false})}>
                <div className={!this.state.search ? "option-selected" : "option-not-selected"}>
                  <h4 className={!this.state.search ? "selected" : "not-selected"}>Related Videos</h4>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
        <div className={this.state.search ? "search-field" : "hidden addedPadding"}>
          <h5 className="heading">Youtube Search:</h5>
            <SearchBar onSearchTermChange={videoSearch} />
          <h6 className="heading">Results:</h6>
          <SearchResults
            videos={this.state.videos}
            related={false}
          />               
       </div>
       <div className={!this.state.search  ? "present playlist-add related" : "hidden addedPadding"}>  
          <h5 className="heading">Related Videos:</h5>      
          <SearchResults
              videos={this.state.relatedVideos}
              related={true}
          />
       </div>

          <div className= "present playlist-add">
              <br/>
              <input type = 'text' className="playlistInput"  onChange={this.onPlaylistImportInputChange} 
                value={this.state.playlistImportInputValue}  placeholder="Insert Playlist URL"
              /> 
              &nbsp;<button className={!this.state.loading ? "button" : "hidden"} onClick={this.getPlaylistService}>Import playlist</button> 
              <img className={this.state.loading ? "" : "hidden"} src={imgLoading} alt="loading" width="32" height="32"/>
          </div>
      </div>
      </Col>
    );
  }
  
}

const mapStateToProps = function(state) {
  return {
    maxResults: state.settings.results,
    onlyHd: state.settings.onlyHd,
    apiKey: state.settings.apiKey,
    playlist: state.playlist
  }
}


const mapDispatchToProps = dispatch => {
  return {
    addPlaylistD: (playlist) => dispatch(addPlaylist(playlist)), 
    addRecommended: (recommended) => dispatch(addRecommended(recommended))
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(SearchField);
