import React from 'react';
import { connect } from 'react-redux';

import YtPlayer from '../ytplayer'
import SearchField from '../searchField'
import VideoContainer from './videoContainer'
import PlaylistContainer from './playlistContainer'

import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css';

 function YtContainer(props) {


    const isLastSong = props.videos.length <= props.mixer.current + 1;
    const isOver = props.videos.length === props.mixer.current    
    const currentVideoId = props.videos.length < 1 || isOver ? null :  props.videos[ props.mixer.current].id.videoId
    const nextVideoId = isLastSong ? null :  props.videos[ props.mixer.current + 1].id.videoId

    const params = {
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        breakpoints: {
            1025: {
                slidesPerView: 3,
                spaceBetween: 30,
                activeSlideKey: 'slide_2',
                noSwiping: false
            },
            360: {
                slidesPerView: 1,
                activeSlideKey: 'slide_2',
                noSwiping: false
            }
        },
        containerClass: 'container',
        noSwiping: true
    }

    const renderVideo = (isFirst) => {

        const playingVideo = (isFirst && props.mixer.first) || (!isFirst && !props.mixer.first)
        const videoId = playingVideo ? currentVideoId : nextVideoId

        return(
            <div align="center"  padding-top="30px" className={playingVideo ? "present" : "hidden"}> 
                        <YtPlayer 
                            videoId={videoId} 
                            shouldPlay={playingVideo && videoId} 
                        />
            </div>
        )
    }

    
    return (  
            <Swiper {...params}>                   
                              
                    <div key='slide_1'><SearchField currentVideoId={currentVideoId}/></div>                   
                    <div key='slide_2'><VideoContainer renderVideo={renderVideo} disableButton={isLastSong || isOver}/></div>
                    <div key='slide_3'><PlaylistContainer videos={props.videos} current={props.mixer.current}/></div>
                
            </Swiper>

    );
    
}


const mapStateToProps = function(state) {
    return {
      videos: state.playlist,
      mixer: state.mixer
    }
}


export default connect(mapStateToProps)(YtContainer);